@import "vars";
@import "mixins";
@import "container";

.header {
  background-image: url("../images/header.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  height: 568px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
  }
  &__action {
    display: none;
  }
  &__logo {
    position: relative;
    z-index: 10;
  }
  &__phone {
    display: none;
    color: $header-font-color;
    @include letter-spacing;
  }
  &__title {
    font-size: 46px;
    font-weight: bold;
    letter-spacing: -1px;
    color: $header-font-color;
  }
  &__promo {
    margin-top: 317px;
    width: 253px;
    &::before {
      content: "";
      left: 3px;
      display: block;
      width: 35px;
      height: 2px;
      background-color: $header-font-color;
    }
  }
}

